<script setup>
import { ref, watch, watchEffect } from "vue";

import { useRouter, useRoute } from "vue-router";
import { processMenu } from "@/components/app/menu/src/processMenu.js";

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

// Funcs
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { formatDateString } from "@/assets/js/functions/formatDateString.js";
import { encodeToHash } from "@/assets/js/functions/encodeToHash.js";
import { returnStatusType } from "@/assets/js/functions/returnStatusType.js";
import { formatCurrency } from "@/assets/js/functions/formatCurrency.js";
import { notify } from "@/assets/js/functions/notify.js";

// Comps
import AppTable from "@/components/utility/table/index.vue";
import StatusTag from "@/components/utility/status-tag/index.vue";
import Multiselect from "@/components/utility/select/index.vue";
import BatchOptions from "@/components/app/batch-options/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import AppIcon from "@/components/utility/icons/index.vue";

// Setup table columns
const columns = ref([
  { key: "name", label: __("Name", "uixpress"), active: true, sort_key: "title" },
  { key: "status", label: __("Status", "uixpress"), active: true },
  { key: "modified", label: __("Modified", "uixpress"), active: true, sort_key: "date" },
  { key: "id", label: __("ID", "uixpress"), active: true, sort_key: "id" },
]);
const filters = [
  { label: __("All", "uixpress"), value: null },
  { label: __("Published", "uixpress"), value: "publish" },
  { label: __("Draft", "uixpress"), value: "draft" },
];

// Refs
const loading = ref(false);
const menu = ref([]);
const creating = ref(false);
const selected = ref([]);
const menus = ref([]);
const router = useRouter();
const route = useRoute();
const pagination = ref({ search: "", per_page: 15, page: 1, pages: 0, total: 0, status: "any", order: "desc", orderby: "date", context: "edit" });

/**
 * Fetches orders
 */
const getMenus = async (suppressLoading) => {
  appStore.updateState("loading", true);
  if (!suppressLoading) loading.value = true;

  const args = { endpoint: "wp/v2/uipxmenus", params: pagination.value };
  const data = await lmnFetch(args);

  appStore.updateState("loading", false);
  loading.value = false;

  if (!data) return;

  menus.value = data.data;

  pagination.value.pages = data.totalPages;
  pagination.value.total = data.totalItems;

  flushCache();
};

/**
 * Fetches orders
 */
const newMenu = async () => {
  appStore.updateState("loading", true);
  creating.value = true;

  const menu_items = [...menu.value];

  const title = __("Draft menu", "uiXpress") + ` (${pagination.value.total})`;
  const args = { endpoint: "wp/v2/uipxmenus", params: {}, data: { title, meta: { menu_items, menu_settings: {} } }, type: "POST" };
  const data = await lmnFetch(args);

  appStore.updateState("loading", false);
  creating.value = false;

  if (!data) return;

  getMenus(true);

  notify({ type: "success", title: __("Menu created", "uixpress") });
  router.push({ name: "menu-editor", params: { menuid: data.data.id } });
};

const handleFirstPage = () => {
  pagination.value.page = 1;
  getMenus(true);
};

const handlePreviousPage = () => {
  if (pagination.value.page > 1) {
    pagination.value.page--;
    getMenus(true);
  }
};

const handleNextPage = () => {
  if (pagination.value.page < pagination.value.pages) {
    pagination.value.page++;
    getMenus(true);
  }
};

const handleLastPage = () => {
  pagination.value.page = pagination.value.pages;
  getMenus(true);
};

const openMenu = (evt, index) => {
  const menuitem = menus.value[index];

  if (!menuitem) return;

  router.push({ name: "menu-editor", params: { menuid: menuitem.id } });
};

const returnStatusFormatted = (status) => {
  if (status == "publish") return __("Published", "uixpress");
  if (status == "draft") return __("Draft", "uixpress");

  return status;
};

const returnTypeFormatted = (status) => {
  if (status == "fixed_product") return __("Fixed product menu", "uixpress");
  if (status == "fixed_cart") return __("Fixed basket menu", "uixpress");
  if (status == "percent") return __("Percentage menu", "uixpress");

  return status;
};

/**
 * Sets the menu
 */
const setMenu = async () => {
  const menuNode = document.querySelector("#adminmenumain");
  const { processedMenu } = await processMenu(menuNode);
  menu.value = processedMenu;
};

const flushCache = () => {
  // Always flush the menu cache on change
  const key = `uixpress_menu_${appStore.state.userID}`;
  localStorage.removeItem(key);
};

watch(
  () => pagination.value.search,
  (newVal, oldVal) => {
    if (!newVal && oldVal) {
      getMenus();
    }
  }
);

watchEffect(() => {
  if (route.query.refresh) {
    getMenus(true);
    route.query.refresh = 0;

    const updatedQuery = { ...route.query };
    delete updatedQuery.refresh;

    router.push({ query: updatedQuery });
  }
});

getMenus();
setMenu();
</script>

<template>
  <div class="p-6">
    <AppTable
      :pagination="pagination"
      :columns="columns"
      :data="menus"
      :fetching="loading"
      :searchPlaceHolder="__('Search menus', 'uixpress')"
      :rowClick="openMenu"
      @selected="(d) => (selected = d)"
      @search="getMenus"
      @previous="handlePreviousPage"
      @next="handleNextPage"
      @first="handleFirstPage"
      @last="handleLastPage"
    >
      <!--Filters-->
      <template v-slot:right-actions>
        <div class="flex flex-row gap-3 items-stretch">
          <BatchOptions v-if="selected.length" v-model="selected" @updated="getMenus(true)" route="wp/v2/uipxmenus" />

          <AppButton type="primary" @click="newMenu" :loading="creating" class="text-sm">{{ __("New menu", "uixpress") }}</AppButton>
        </div>
      </template>

      <!-- Empty slot-->
      <template v-slot:empty>
        <div class="flex flex-col gap-4 p-12 items-center">
          <AppIcon icon="folder_open" style="font-size: 60px" />
          <div class="font-bold text-xl text-zinc-900 dark:text-zinc-100">{{ __("No menus yet", "uixpress") }}</div>
          <div class="text-zinc-400">{{ __("When you create new menus they will show up here.", "uixpress") }}</div>
          <AppButton type="primary" class="mt-4" @click="newMenu" :loading="creating">{{ __("New menu", "uixpress") }}</AppButton>
        </div>
      </template>

      <!-- code -->
      <template v-slot:row-name="{ row }">
        <div class="inline-flex text-zinc-900 dark:text-zinc-100 font-semibold" v-html="row.title.rendered"></div>
      </template>

      <!-- code -->
      <template v-slot:row-code="{ row }">
        <div class="inline-flex text-zinc-900 dark:text-zinc-100 font-semibold">
          {{ row.code }}
        </div>
      </template>

      <!-- Status -->
      <template v-slot:row-status="{ row }">
        <div class="inline-flex text-zinc-500">
          <StatusTag class="text-sm" :status="returnStatusType(row.status)" :text="returnStatusFormatted(row.status)" />
        </div>
      </template>

      <!-- modified -->
      <template v-slot:row-modified="{ row }">
        <div class="inline-flex">{{ formatDateString(row.modified) }}</div>
      </template>

      <!-- Menu id -->
      <template v-slot:row-id="{ row }">
        <div class="inline-flex text-sm truncate">
          {{ row.id }}
        </div>
      </template>
    </AppTable>
  </div>
</template>
